import React from 'react';
import { Helmet } from 'react-helmet';
import { useConfirmationApi } from 'shared/hooks';
import { useHistory } from 'react-router';

function AuthTransferOwner() {
	const history = useHistory();

	//Custom hook to handle confirming links from email
	useConfirmationApi(handleRedirect, true);

	function handleRedirect() {
		history.push('/case-messages');
	}

	return (
		<div className={'h-full w-full p-8'}>
			<>
				<Helmet>
					<title>KENNEKT | Transfer Owner processing</title>
				</Helmet>
				<h1 className={'text-center text-xl font-bold'}>
					Wait... you will be redirected appropriate page.
				</h1>
			</>
		</div>
	);
}

export default AuthTransferOwner;
